.footer{
    margin-top: 4rem;
    background-image: linear-gradient(to right, red , orange);
    padding: 10rem 3rem;
    border-top-left-radius: 150px;
    display: flex;
    justify-content: space-between;
}
.white_logo{
    width: 180px;
}
.footer_about{
    color: #fff;
    font-weight: bold;
    font-size: 1.1rem;
    width: 250px;
    margin-right: 1rem;
    margin-top: 1rem;
}
.footer h2{
    color: #fff;
    margin-bottom: 0.5rem;
}
.catogery_container{
    display: flex;
}
.catogery_container div:nth-child(2){
    margin-right: 2rem;
}
.catogery_container p,
.info{
    color: #fff;
    font-weight: bold;
    font-size: 1.1rem;
    margin-top: 0.4rem;
}
.footer_img{
    width: 30px !important;
    margin-bottom: -0.5rem;
    margin-left: 1rem;
}
.info{
    display: inline-block;
    margin: 0.6rem 0rem;
}
.social_icon{
    display: flex;
}
.social_icon img{
    width: 38px;
    height: 38px;
    margin-top: 0.8rem;
    margin-left: 0.8rem;
    cursor: pointer;
}
/* media screen device
------------------------ */
/* for large device */
@media (max-width: 1070px) {
    .footer{
        padding: 10rem 1rem;
        align-items: center;
        flex-direction: column;
    }
    .footer_about{
        width: 350px;
        margin-right: 0rem;
        text-align: center;
    }
    .white_logo{
        margin-right: 5rem;
    }
    .footer h2{
        margin-top: 2.5rem;
        text-align: center;
    }
    .social_icon{
        justify-content: center;
    }
}
/* for small device */
@media (max-width: 400px) {
    .footer_about{
        width: 320px;
    }
    .catogery_container div:nth-child(2){
        margin-right: 0.5rem;
    }
}

