.order-container{
    padding-top: 10rem;
    width: 60%;
    margin: auto;
}
.order-title{
    color: #000;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
}
.product-name-label{
    color: #2E2A39;
    font-size: 1.2rem;
    font-weight: bold;
    display: block;
    margin: 0.8rem 0rem; 
}
.order-product-name{
    color: #2E2A39;
    font-size: 1.1rem;
    font-weight: bold;
    border: solid 1px rgb(182, 181, 181);
    padding: 0.5rem 0rem;
    padding-right: 0.8rem;
    border-radius: 20px;
    margin: 0.4rem 0rem;
    width: 100%;
}
.order-input{
    width: 100%;
    border: solid 1px rgb(182, 181, 181);
    padding: 0.5rem 0rem;
    padding-right: 0.8rem;
    border-radius: 20px;
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
}
select {
    -moz-appearance:none; /* Firefox */
    -webkit-appearance:none; /* Safari and Chrome */
    appearance:none;
}
.phone-number{
    display: flex;
}
.phone-number div{
    width: 50%;
}
.phone-number div:nth-child(2){
    margin-right: 4rem;
}
.product-name-label span{
    color: #f68f14;
}
.shipping{
    display: flex;
}
.shipping div:nth-child(1){
    width: 80%;
}
.shipping div:nth-child(2){
    width: 35%;
    margin-right: 3rem;
}
.shipping-price{
    color: #2E2A39;
    font-size: 1.1rem;
    font-weight: bold;
    border: solid 1px rgb(182, 181, 181);
    padding: 0.5rem 0rem;
    padding-right: 0.8rem;
    border-radius: 20px;
    margin-bottom: 0.5rem;
    width: 100%;
}
.color{
    width: 102%;
}
.quantity{
    display: flex;
}
.quantity div:nth-child(1){
    width: 50%;
}
.quantity div:nth-child(2){
    width: 50%;
    margin-right: 4rem;
}
.order-button{
    width: 220px;
    border: 0px;
    color: #fff;
    background-color: #f68f14;
    height: 40px;
    font-weight: bold;
    font-size: 1.2rem;
    margin-top: 1rem;
    border-radius: 10px;
    cursor: pointer;
}
.end-error{
    font-weight: bold;
    color: rgb(116, 32, 32);
    margin-top: 0.7rem;
}
.loader-order {
    width: 25px;
    height: 25px;
    border: 2.5px dotted #FFF;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    box-sizing: border-box;
    margin-top: 0.2rem;
    animation: rotation 2.5s linear infinite;
  }
  
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
} 
.error{
    color: red;
    font-weight: bold;
    font-size: 1.1rem;
}
.show-error{
    display: initial;
}
.order_layer{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 11111111;
}
.black-layer{
    background-color: #000;
    opacity: 0.6;
    position: absolute;
    width: 100%;
    height: 100%;
}
.order_layer_content{
    background-color: #fff;
    width: 35%;
    position: relative;
    margin: auto;
    margin-top: 12rem;
    padding: 1rem;
    padding-bottom: 1.6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px !important;
}
.order_layer_content img{
    width: 70px;
}
.order_layer_content p{
    width: 300px;
    font-weight: bold;
    color: #2E2A39;
    line-height: 1.5rem;
    text-align: center;
}
.order_layer_content button{
    border: 0px;
    color: #fff;
    background-color: #f68f14;
    padding: 0.6rem 1rem;
    font-weight: bold;
    font-size: 1rem;
    margin-top: 0.7rem;
    border-radius: 10px;
    cursor: pointer;
}

/* for large device */
@media (max-width: 1030px) {
    .order_layer_content{
        width: 45%;
    }
    .order_layer_content p{
        width: 350px;
    }
}
  
  @media (max-width: 960px) {
    .order_layer_content{
        width: 55%;
    }
}

/* for medium device */
@media (max-width: 760px) {
    .order-container{
        padding-top: 6rem;
        width: 80%;
    }
    .order_layer_content{
        width: 65%;
    }
}  
@media (max-width: 600px) {
    .phone-number{
        flex-direction: column;
    }
    .phone-number div{
        width: 100%;
    }
    .phone-number div:nth-child(2){
        margin-right: 0rem;
    }
    .shipping div:nth-child(1){
        width: 60%;
    }
    .shipping div:nth-child(2){
        width: 40%;
        margin-right: 2rem;
    }
    .order_layer_content{
        width: 75%;
    }
    .color{
        width: 103%;
    }
}
@media (max-width: 505px) {
    .order-container{
        width: 90%;
        margin-right: 0px;
        padding-right: 0.5rem;
       
    }
    .shipping div:nth-child(1){
        width: 60%;
    }
    .shipping div:nth-child(2){
        width: 50%;
        margin-right: 2rem;
    }
    .quantity div:nth-child(2){
        margin-right: 1.5rem;
    }
    .order_layer_content{
        width: 85%;
    }
    .color{
        width: 104.1%;
    }
}
@media (max-width: 420px) {
    .shipping div:nth-child(1){
        width: 40%;
    }
    .shipping div:nth-child(2){
        width: 60%;
        margin-right: 0.8rem;
    }
    .order_layer_content{
        width: 88%;
    }
    .order_layer_content p{
        width: 300px;
    }
}
  