.navbar{
    padding: 1.5rem 0rem;
    position: fixed;
    width: 100%;
    background-color: #fff;
}
.nav-container{
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}
.nav-icon{
    display: flex;
}
.nav-icon img{
    width: 25px;
    height: 27px;
    cursor: pointer;
}
.nav-icon img:first-child{
    margin-left: 0.6rem;
}
.nav-logo img{
    width: 180px;
}
.nav-notification img{
    width: 25px;
    height: 29px;
    cursor: pointer;
}
.nav-menu{
    display: flex;
    justify-content: center;
}
.nav-link{
    color: #2E2A39;
    font-size: 1.2rem;
    font-weight: bold;
    margin: 0rem 0.6rem;
}
.active{
    color: #f68f14 !important;
}
/* responsive navbar */
.responsive-navbar{
    padding: 1.5rem 0rem;
    position: fixed;
    width: 100%;
    background-color: #fff;
    display: none;
}
.nav-container-res{
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}
.nav-toggle img{
    width: 27px;
    height: 25px;
    margin-top: 0.2rem;
    cursor: pointer;
}
.nav-menu-res{
    padding: 0;
    width: 90%;
    margin: auto;
    margin-top: 0.8rem;
    border: 1px solid rgb(145, 145, 145);
    border-radius: 20px;
    display: none;
}
.nav-menu-res li{
    width: 100%;
    text-align: center;
    padding: 0.6rem 0rem;
    border-bottom: 1px solid rgb(145, 145, 145);
}
.nav-menu-res li:last-child{
    width: 100%;
    text-align: center;
    padding: 0.6rem 0rem;
    border-bottom: none;
}
.nav-link-res{
    color: #2E2A39;
    font-size: 1.2rem;
    font-weight: bold;
}
.hide{
    margin-top: -150px;
    transition: 0.4s;
}
.show{
    margin-top: 0;
    transition: 0.4s;
}
/* media screen device
------------------------ */
/* for medium device */
@media (max-width: 767px) {
    .responsive-navbar{
        display: initial;
    }
    .navbar{
        display: none;
    }
}