.details {
    padding-top: 10rem;
    width: 80%;
    margin: auto;
}
.details-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.product-img {
    width: 45%;
    overflow: hidden;
}
.product-img img {
    width: 100%;
    border-radius: 20px;
}
.sub-product-img {
    display: flex;
}
.sub-product-img img {
    width: 24.05%;
    margin-left: 0.4rem;
    border-radius: 0px;
    cursor: pointer;
}
.d-initail{
    display: initial;
}
.product-details {
    width: 50%;
    margin-right: 2rem;
    margin-top: 2rem;
}
.product-details p {
    color: #2E2A39;
    font-weight: bold;
}
.product-name-details{
    font-family:Georgia, 'Times New Roman', Times, serif ;
    font-size: 1.6rem !important;
}
.product-price p{
    font-size: 1.2rem;
}
.product-price p:nth-child(2) {
    text-decoration: line-through;
    color: gray;
    margin-right: 0.5rem;
    margin-top: 0.2rem;
}
.product-price p span {
    color: #f68f14;
}
.product-price{
    display: flex;
    margin-top: 0.4rem;
}
.product-code{
    color: gray !important;
    font-size: 1.1rem;
    margin-top: 0.3rem;
}
.product-code span{
    color: #000;
}
.line{
    width: 100%;
    height: 1px;
    background-color: rgb(218, 218, 218);
    margin-top: 2rem;
}
.product-status{
    margin-top: 1.2rem;
    color: gray !important;
}
.product-status span{
    color: #f68f14;
}
.product-desc{
    margin: 1rem 0rem;
    font-size: 1.2rem;
}
.desc_item{
    margin-bottom: 0.5rem;
}
.product-details button{
    width: 40%;
    margin-top: 2rem;
    border: 0px;
    color: #fff;
    background-color: #f68f14;
    padding: 0.6rem;
    border-radius: 10px;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
}
/* media screen device
------------------------ */
/* for large device */
@media (max-width: 1030px) {
    .product-img {
        width: 60%;
    }
    .product-details {
        width: 60%;
        margin-right: 0rem;
    }
}
/* for medium device */
@media (max-width: 760px) {
    .product-img {
        width: 80%;
    }
    .product-details {
        width: 80%;
    }
    .details {
        padding-top: 6rem;
    }
}
@media (max-width: 600px) {
    .product-img {
        width: 90%;
    }
    .product-details {
        width: 90%;
    }
    .details {
        width: 90%;
    }
    .not_avilabel{
        width: 80% !important;
    }
}
@media (max-width: 450px) {
    .product-img {
        width: 90%;
    }
    .product-details {
        width: 90%;
    }
    .details {
        width: 100%;
    }
    .sub-product-img img {
        width: 23%;
        margin-left: 0.5rem;
    }
}